.piechart_container {
  width: 91%;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 1rem;
  margin: 0rem 0rem 0.5rem 0.5rem;
  display: flex;
  justify-content: center;
  background-color: white;

  .piechart {
    width: 100%;
    height: 50%;
  }
}
@media screen and (max-width: 600px) {
  .piechart_container {
    width: 87%;
    .piechart {
      width: 100%;
      height: 50%;
    }
  }
}
