.lineChart_container {
  width: 100%;
  border: 1px solid lightgray;
  border-radius: 10px;
  margin: 0.5rem;

  .lineChart {
    width: 100%;
    height: 50%;
  }
}
@media screen and (max-width: 600px) {
  .lineChart_container {
    width: 95.5%;

    .lineChart {
      width: 100%;
      height: 100%;
    }
  }
}
