.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App_content {
  display: flex;
  flex-direction: row;
}

.App_left {
  width: 20%;
  display: flex;
  background-color: aliceblue;
}

.App_middle {
  width: 38%;
  display: flex;
  flex-direction: column;
}

.App_right {
  width: 40%;
}

.daywise_activity {
  border: 1px solid lightgray;
  border-radius: 10px;
  margin: 0.5rem;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .App_left {
    width: 100%;
    overflow: hidden;
  }

  .App_content {
    display: flex;
    flex-direction: column;
  }

  .App_middle {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .App_right {
    width: 100%;
  }

  .daywise_activity {
    border: 1px solid lightgray;
    border-radius: 10px;
    margin: 0.5rem;
    width: 95.5%;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}