.header_container {
  background-color: rgb(215, 235, 253);
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem;
  align-items: center;
  .header_text {
    font-size: 2rem;
    font-weight: 600;
  }
  .header_button {
    background-color: transparent;
    border: 2px solid gray;
    padding: 0.5rem 0.6rem;
    border-radius: 100%;
    cursor: pointer;
  }
}
@media screen and (max-width: 600px) {
  .header_container {
    width: 91.5%;
    justify-content: center;
    .header_text {
      font-size: 1rem;
      font-weight: 600;
    }
    .header_button {
      background-color: transparent;
      border: 2px solid gray;
      padding: 0.3rem 0.3rem;
      border-radius: 100%;
      margin-left: 0.5rem;
      cursor: pointer;
    }
  }
}
